class jplus {
   static add(methodName: string, method: Function) {
     // Inject method into HTMLElement
     HTMLElement.prototype[methodName] = function () {
       return method(this, ...arguments);
     };

     // Make it method enumerable
     Object.defineProperty(Object.prototype, methodName, { enumerable: false });
   }
}

exports.jplus = jplus

exports.$ = function $(query: string): Element | Element[]  {
    // IF : query is HTML element stracture
    if ( query.trim()[0] === '<' ) {
        const div = document.createElement('div')
        div.innerHTML = query
        return div.children[0]
    }

    const result: NodeListOf<any> = document.querySelectorAll(query);

    // IF : resutl is multiable of elements return it else return one element
    return result.length === 1 ? result[0] : result
};